import * as React from "react";
import Main_AppBar from "./components/Main_AppBar";
import {
  Box,
  Button,
  Checkbox,
  Container,
  FormControlLabel,
  Typography,
} from "@mui/material";
import MainFooter from "./components/MainFooter";
import MainContext from "../context";

const RulesPage = () => {
  const [accRules, setAccRules] = React.useState(false);
  const { handlePageNumber } = React.useContext(MainContext);
  const handleSendSelection = (event) => {
    setAccRules(event.target.checked);
  };
  return (
    <>
      <Box
        sx={{
          backgroundColor: "#77d177",
          width: "100%",
          maxWidth: "100%",
        }}
      >
        <Main_AppBar />
        <Container
          sx={{
            position: "absolute",
            height: "55vh",
            width: "90vw",
            top: "55%",
            left: "50%",
            transform: "Translate(-50%,-50%)",
            backgroundColor: "lightcyan",
            borderRadius: "15px",
          }}
        >
          <Typography variant="h4" sx={{ textAlignLast: "center", mt: "2vw" }}>
              
          </Typography>
          <Typography
            variant="h5"
            sx={{ textAlignLast: "left", mt: "2vw", textAlign: "justify" }}
          >
            -            
                 .       
                        .
          </Typography>
          <Typography
            variant="h5"
            sx={{ textAlignLast: "left", mt: "2vw", textAlign: "justify" }}
          >
            -    ()         
                  .
          </Typography>
          <Typography
            variant="h5"
            sx={{ textAlignLast: "left", mt: "2vw", textAlign: "justify" }}
          >
            -             
                 (         
                      ).
          </Typography>
          <FormControlLabel
            sx={{ mt: "3vw", color: "red" }}
            control={
              <Checkbox
                color="success"
                size="medium"
                sx={{ color: "lightblue" }}
                onClick={(e) => handleSendSelection(e)}
              />
            }
            label="      ."
          />
          <Button
            variant="contained"
            sx={{
              left: "30%",
              mt: "4%",
              backgroundColor: "#001B48",
              color: "white",
              ":hover": {
                backgroundColor: "#F7AD19",
                color: "#001B48",
              },
            }}
            disabled={!accRules}
            onClick={() => handlePageNumber(7)}
          >
              
          </Button>
        </Container>
        <Box
          sx={{
            bgcolor: "red",
            height: "40vh",
            width: "100%",
            top: "100vh",
            position: "absolute",
          }}
        >
          <MainFooter />
        </Box>
      </Box>
    </>
  );
};

export default RulesPage;
